import React, { memo } from "react";
import style from "./CoAttainmentTable.module.css";

const CoAttainmentTableHeadingNested = ({ firstRow, sedondRow, thirdRow }) => {
  // console.info("Sdfkjjsdhgfkj", firstRow);
  return (
    <>
      <div className={style.tabel_heading_nested_inner}>
        {firstRow?.map((first, index) => (
          <div
            className={style.tabel_nested_text}
            key={index}
            style={{
              width: index === 0 ? "10rem" : "",
            }}
          >
            {first ?? ""}
          </div>
        ))}
      </div>
      {sedondRow?.length > 0 ? (
        <div className={style.tabel_heading_nested_inner}>
          {sedondRow?.map((first, index) => (
            <div
              className={style.tabel_nested_text}
              key={index}
              style={{
                width: index === 0 ? "10rem" : "",
              }}
            >
              {first ?? ""}
            </div>
          ))}
        </div>
      ) : null}
      {thirdRow?.length > 0 ? (
        <div className={style.tabel_heading_nested_inner}>
          {thirdRow?.map((first, index) => (
            <div
              className={style.tabel_nested_text}
              key={index}
              style={{
                width: index === 0 ? "10rem" : "",
              }}
            >
              {first ?? ""}
            </div>
          ))}
        </div>
      ) : null}
    </>
  );
};

export default memo(CoAttainmentTableHeadingNested);
